/* =========================================================
body
========================================================= */
html {
  font-size: 62.5%;
}

body {
  background-size: cover;
  color: #1D5D00;
  font-family: "source-han-sans-cjk-ja", sans-serif;
  font-weight: 500;
  font-style: 400;
  font-size: 20px;
  line-height: 1.8;
}

@media (max-width: 768px) {
  body {
    font-size: 16px;
  }
}

body.open_modal {
  overflow: hidden;
}

.menu-fixed a {
  font-family: span;
}

.title h2 span, .viewmore, .pft p {
  font-family: span;
}

.main-contents {
  font-weight: 500;
}

@media print {
  html, body {
    _zoom: 70% !important;
  }
}

main {
  display: block;
  /*IE*/
}

/* =========================================================
default
========================================================= */
/* =========================================================
PARCO Header
========================================================= */
.phd {
  padding: 0 10px;
  background: #fff;
  border-bottom: 1px solid #ccc;
  overflow: hidden;
  position: relative;
  z-index: 22;
}

.phd__in {
  height: 55px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  box-sizing: border-box;
}

.phd__logo a {
  display: inline-block;
  line-height: 0;
  font-size: 0;
  text-decoration: none;
}

.phd__logo__txt {
  width: 13px;
  height: 26px;
  margin-right: 2px;
}

.phd__logo__img {
  width: 92px;
  height: 27px;
  vertical-align: bottom;
  margin-bottom: -1px;
}

.phd__logo__img.parcoya-ueno {
  width: 155px;
  height: 25px;
}

.phd__sns {
  display: flex;
  align-items: center;
  justify-content: center;
}

.phd__sns li {
  margin-right: 8px;
}

.phd__sns li:last-child {
  margin-right: 0;
}

.phd__sns li a {
  display: flex;
  align-items: center;
  width: 35px;
  height: 35px;
  background: #222;
  border-radius: 50%;
}

@media screen and (min-width: 769px) {
  .phd__sns li a {
    width: 28px;
    height: 28px;
  }
  .phd__sns li a:hover {
    opacity: 0.7;
  }
}

.phd__sns li a svg {
  fill: #FFF;
  width: 100%;
}

@media screen and (min-width: 769px) {
  .phd {
    padding: 0 20px;
  }
  .phd__in {
    height: 64px;
  }
  .phd__logo__txt {
    display: inline-block;
    font-size: 1.5rem;
    width: 14px;
    height: 32px;
    margin-right: 4px;
    vertical-align: middle;
  }
  .phd__logo__img {
    width: 120px;
    height: 36px;
    vertical-align: middle;
  }
  .phd__logo__img.parcoya-ueno {
    width: 205px;
    height: 34px;
  }
}

.phd__sns li .phd__sns__item__link-facebook {
  background: transparent;
}

.phd__sns li .phd__sns__item__link-facebook svg {
  height: 35px;
}

@media screen and (min-width: 769px) {
  .phd__sns li .phd__sns__item__link-facebook svg {
    height: 28px;
  }
}

.phd__sns li .phd__sns__item__link-twitter {
  background: #3AB1DB;
}

.phd__sns li .phd__sns__item__link-twitter svg {
  height: 16px;
}

@media screen and (min-width: 769px) {
  .phd__sns li .phd__sns__item__link-twitter svg {
    height: 11px;
  }
}

.phd__sns li .phd__sns__item__link-lineat {
  background: transparent;
}

.phd__sns li .phd__sns__item__link-lineat svg {
  height: 35px;
}

@media screen and (min-width: 769px) {
  .phd__sns li .phd__sns__item__link-lineat svg {
    height: 28px;
  }
}

.phd__sns li .phd__sns__item__link-instagram {
  background: #d93174;
}

.phd__sns li .phd__sns__item__link-instagram svg {
  height: 19px;
}

@media screen and (min-width: 769px) {
  .phd__sns li .phd__sns__item__link-instagram svg {
    height: 15px;
  }
}

.phd__sns li .phd__sns__item__link-tiktok {
  background: #000000;
}

.phd__sns li .phd__sns__item__link-tiktok svg {
  height: 19px;
}

@media screen and (min-width: 769px) {
  .phd__sns li .phd__sns__item__link-tiktok svg {
    height: 17px;
  }
}

/* =========================================================
PARCO Footer
========================================================= */
.pft {
  padding: 80px 0 148px 0;
  background: #F8EFD9;
  position: relative;
  color: #FF4A01;
}

@media (max-width: 1023px) {
  .pft {
    padding: 50px 0 90px 0;
  }
}

.pft .logo {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 26px;
  max-width: 158px;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 767px) {
  .pft .logo {
    margin-bottom: 18px;
  }
}

.pft .logo a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.pft p {
  font-size: 10px;
  font-weight: bold;
  letter-spacing: 0.75px;
}

.social-footer {
  padding: 28px 0 0 0;
}

@media (max-width: 768px) {
  .social-footer {
    padding: 14px 0 0 0;
  }
}

.social-footer ul {
  display: flex;
  align-items: center;
  justify-content: center;
}

.social-footer ul li {
  padding: 0 12px;
}

@media (max-width: 768px) {
  .social-footer ul li {
    padding: 0 12px;
  }
}

.social-footer a {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 768px) {
  .social-footer img {
    width: 26px;
    height: 26px;
  }
}

.content-header {
  position: fixed;
  top: -80px;
  left: 0;
  width: 100%;
  z-index: 14;
  padding: 20px;
  transition: 1s all !important;
  opacity: 0;
}

@media (max-width: 767px) {
  .content-header {
    padding: 18px 10px;
  }
}

.content-header.active {
  top: 0;
  opacity: 1;
}

.content-header ul {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content-header ul li {
  margin-right: 26px;
}

@media (max-width: 767px) {
  .content-header ul li {
    margin-right: 20px;
  }
}

.content-header ul li:last-child {
  margin-right: 0;
}

.content-header ul li img {
  height: 29px;
}

@media (max-width: 767px) {
  .content-header ul li img {
    height: 18px;
  }
}

.content-header ul li img {
  max-height: 30px;
}

.content-header .h-header {
  display: flex;
  justify-content: space-between;
}

.content-header .h-header .logo img {
  width: 220px;
}

@media (max-width: 767px) {
  .content-header .h-header .logo img {
    width: auto;
    height: 104px;
  }
}

.content-header .logo {
  position: relative;
  animation: 5s fuwafuwa infinite;
  top: -20px;
}

@media (max-width: 767px) {
  .content-header .logo {
    top: -10px;
  }
}

.menu-fixed {
  position: fixed;
  bottom: 80px;
  z-index: 14;
  left: 0;
  width: 100%;
  padding: 23px 0;
  background: #fff;
  transition: 1s all !important;
  opacity: 0;
}

@media (max-width: 767px) {
  .menu-fixed {
    padding: 10px;
  }
}

.menu-fixed.active {
  bottom: 0;
  opacity: 1;
}

.menu-fixed ul {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 767px) {
  .menu-fixed ul {
    justify-content: space-between;
  }
}

.menu-fixed ul li {
  margin-right: 40px;
  line-height: initial;
}

@media (max-width: 767px) {
  .menu-fixed ul li {
    margin-right: 10px;
  }
}

.menu-fixed ul li:last-child {
  margin-right: 0;
}

.menu-fixed a {
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #1D5D00;
  font-size: 20px;
  font-weight: normal;
}

@media (max-width: 767px) {
  .menu-fixed a {
    font-size: 14px;
  }
}

.icon-star {
  position: absolute;
}

.icon-star.star-1 {
  top: 0;
  right: 14%;
  max-width: 20%;
  animation: letter-flicker ease-out 1s infinite;
  z-index: 3;
}

@media (max-width: 767px) {
  .icon-star.star-1 {
    width: 216px;
    max-width: unset;
    top: 15%;
    right: 0;
    z-index: 1;
  }
}

.icon-star.star-2 {
  left: 0;
  top: 42%;
  max-width: 20%;
  animation: letter-flicker ease-out 1.6s infinite;
}

@media (max-width: 767px) {
  .icon-star.star-2 {
    width: 216px;
    max-width: unset;
    top: 37%;
    left: 0;
  }
}

.icon-star.star-3 {
  bottom: -12%;
  right: 2%;
  max-width: 20%;
  animation: letter-flicker ease-out 1s infinite;
}

@media (max-width: 767px) {
  .icon-star.star-3 {
    width: 210px;
    max-width: unset;
    bottom: -17%;
    right: 0;
  }
}

