/* =========================================================
ヘッダー下バナー
========================================================= */
.top-bnr-block {
  max-width: 596px;
  margin: 0 auto;
  padding: 20px 0 0;
}

.top-bnr-block .bnr {
  margin: 0 auto 5px;
}

.top-bnr-block .bnr:last-child {
  margin-bottom: 0;
}

@media screen and (min-width: 769px) {
  .top-bnr-block {
    max-width: 930px;
    padding: 60px 0 0;
  }
  .top-bnr-block .bnr {
    margin: 0 auto 20px;
  }
}

/* =========================================================
YouTube
========================================================= */
.youtube-block {
  max-width: 1024px;
  margin: 0 auto;
  padding: 20px 0 0;
}

.youtube-wrap {
  padding-top: 56.25%;
  position: relative;
}

.youtube-wrap iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (min-width: 769px) {
  .youtube-block {
    padding: 60px 0 0;
  }
}

/* =========================================================
Local navigation
========================================================= */
.localnav-wrap {
  max-width: 1024px;
  padding: 30px 0 0;
  margin: 0 auto;
}

.localnav {
  padding: 0 10px;
  display: flex;
  justify-content: center;
}

.localnav__item {
  width: calc(50% - 5px);
  position: relative;
  margin: 0 10px 0 0;
}

.localnav__item:last-child {
  margin-right: 0;
}

.localnav__item a {
  display: block;
  padding: 7px 0;
  text-align: center;
  background: #f2ec27;
  font-size: 1.6rem;
  font-weight: bold;
  position: relative;
  color: #000;
  border-radius: 5px;
  text-decoration: none;
}

.localnav__item a:after {
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 8px 8px 0 8px;
  border-color: transparent transparent transparent transparent;
  position: absolute;
  bottom: -8px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}

@media screen and (min-width: 769px) {
  .localnav-wrap {
    padding: 60px 0 0;
  }
  .localnav {
    padding: 0;
  }
  .localnav__item {
    width: calc(50% - 20px);
    margin: 0 40px 0 0;
  }
  .localnav__item a {
    padding: 23px 0;
    font-size: 2.1em;
    border-radius: 8px;
  }
  .localnav__item a:after {
    border-width: 8px 6px 0 6px;
  }
  .localnav__item a:hover {
    opacity: 0.7;
    text-decoration: none;
    background: #f2ec27;
    color: #000;
  }
}

/* current */
body.page-sale .localnav__item01 a,
body.page-tag .localnav__item02 a {
  background: #f2ec27;
  color: #000;
}

body.page-sale .localnav__item01 a:after,
body.page-tag .localnav__item02 a:after {
  border-color: #f2ec27 transparent transparent transparent;
}

/* =========================================================
Contents block
========================================================= */
.contents-wrap {
  max-width: 1024px;
  margin: 40px auto 0;
}

.contents-block {
  margin: 0 0 40px;
}

.contents-block:last-child {
  margin-bottom: 0;
}

.contents-ttl {
  margin: 0 auto 25px;
  padding: 9px 0;
  background: #000;
  color: #fff;
  line-height: 1.1;
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 2rem;
  letter-spacing: 0.15em;
  position: relative;
}

@media screen and (min-width: 769px) {
  .contents-wrap {
    margin: 80px auto 0;
  }
  .contents-block {
    margin: 0 0 100px;
  }
  .contents-ttl {
    margin: 0 auto 70px;
    font-size: 3rem;
  }
}

/* =========================================================
タグ集約
========================================================= */
.taglist {
  overflow: hidden;
}

.taglist__item {
  border-bottom: 1px solid #ccc;
  margin-bottom: 15px;
  list-style: none;
  display: none;
}

.taglist__item:first-child {
  border-top: 1px solid #ccc;
}

.taglist__item:last-child {
  margin-bottom: 0;
}

.taglist__item:nth-child(-n+6) {
  /* 初期表示6件 */
  display: block;
}

.taglist__item__in {
  height: 100%;
}

.taglist__item a {
  display: block;
  position: relative;
  height: 100%;
  padding: 15px;
  color: #555;
  background: #fff;
  font-size: 1.4rem;
  line-height: 1.5;
  text-decoration: none;
  box-sizing: border-box;
}

.taglist__item a:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 0 10px 10px;
  border-color: transparent transparent #555 transparent;
  position: absolute;
  right: 4px;
  bottom: 4px;
}

.taglist__txt {
  margin-top: 15px;
}

.taglist__photo {
  position: relative;
  padding: 0 40px;
}

.taglist__kaeru {
  position: absolute;
  top: 0;
  left: 40px;
  font-size: 10px;
  font-size: 1rem;
  color: #fff;
  line-height: 1.1;
  display: inline-block;
  background: #ea5550;
  padding: 7px 5px;
}

.taglist__name {
  font-size: 12px;
  font-size: 1.2rem;
  margin-bottom: 5px;
}

.taglist__title {
  font-weight: bold;
  margin-bottom: 6px;
}

@media screen and (min-width: 769px) {
  .taglist {
    display: flex;
    flex-wrap: wrap;
  }
  .taglist__item {
    width: calc(33.33% - 10px);
    margin: 15px 15px 0 0;
    border: 1px solid #ccc;
    box-sizing: border-box;
  }
  .taglist__item:nth-child(-n+3) {
    margin-top: 0;
  }
  .taglist__item:nth-child(3n) {
    margin-right: 0;
  }
  .taglist__photo {
    padding: 0;
  }
}

.btn-taglist-more {
  display: none;
  width: calc(100% - 150px);
  max-width: 435px;
  margin: 30px auto 0;
  padding: 15px 0;
  background: #000;
  border-radius: 10px;
  border: none;
  color: #fff;
  font-size: 2rem;
  font-family: "Roboto Condensed", sans-serif;
  cursor: pointer;
  letter-spacing: 0.15em;
  font-weight: bold;
}

@media screen and (min-width: 769px) {
  .btn-taglist-more {
    margin: 40px auto 0;
  }
  .btn-taglist-more:hover {
    opacity: 0.7;
  }
}

/* =========================================================
OWNLY
========================================================= */
.ownly-wrap {
  padding: 0 10px;
}

@media screen and (min-width: 769px) {
  .ownly-wrap {
    padding: 0;
  }
}

/* =========================================================
値書き
