  color: #fff;
  background: #358BD3;
  position: relative;
}

@media (max-width: 1023px) {
  .box-bgm {
    padding: 30px 0 100px 0;
  }
}

.box-bgm .top-bg {
  background: url("../images/top-bgm.png") repeat-x center;
}

@media (max-width: 767px) {
  .box-bgm .title {
    margin-bottom: 28px;
  }
}

.box-bgm .icon-abs {
  position: absolute;
}

.box-bgm .icon-abs.abs-1 {
  top: -18%;
  left: -10%;
  max-width: 34%;
}

@media (max-width: 767px) {
  .box-bgm .icon-abs.abs-1 {
    max-width: 224px;
    left: -92px;
    top: -8%;
  }
}

.box-bgm .icon-abs.abs-2 {
  bottom: -26%;
  right: -20%;
  max-width: 32.5%;
}

@media (max-width: 1600px) {
  .box-bgm .icon-abs.abs-2 {
    bottom: -26%;
    right: -14%;
    max-width: 28%;
  }
}

@media (max-width: 1400px) {
  .box-bgm .icon-abs.abs-2 {
    bottom: -26%;
    right: -10%;
    max-width: 28%;
  }
}

@media (max-width: 1360px) {
  .box-bgm .icon-abs.abs-2 {
    bottom: -26%;
    right: 0;
    max-width: 28%;
  }
}

@media (max-width: 1023px) {
  .box-bgm .icon-abs.abs-2 {
    bottom: -22%;
  }
}

@media (max-width: 767px) {
  .box-bgm .icon-abs.abs-2 {
    max-width: 218px;
    right: -16%;
  }
}

.content-bgm {
  max-width: 720px;
  margin: 0 auto;
}

.content-bgm .sort-content {
  font-size: 20px;
  font-weight: 500;
  line-height: 2;
}

@media (max-width: 767px) {
  .content-bgm .sort-content {
    font-size: 14px;
  }
}

.content-bgm .viewmore {
  margin-top: 60px;
}

@media (max-width: 767px) {
  .content-bgm .viewmore {
    margin-top: 28px;
  }
}

.box-banner {
  position: relative;
  height: 100vh;
  background: #40CE7A;
  overflow: hidden;
  z-index: 10;
}

@media (max-width: 767px) {
  .box-banner {
    display: none;
  }
}

.box-banner .container {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 1360px;
}

.box-banner .icon-abs {
  position: absolute;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  z-index: 11;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.box-banner .icon-abs img {
  max-width: 100%;
  max-height: 100%;
  width: 100%;
}

.box-banner .icon-abs.icon-1 {
  filter: blur(10px);
  transition: .4s all !important;
}

.box-banner .icon-abs.icon-1.active {
  filter: blur(0);
}

.box-banner .icon-abs.icon-2 {
  transition: .7s all !important;
  top: -60px;
  opacity: 0;
}

.box-banner .icon-abs.icon-2.active {
  opacity: 1;
  top: 0;
  animation: 5s fuwafuwa infinite;
}

.box-banner .icon-abs.icon-3 {
  transition: .7s all !important;
  top: -40px;
  opacity: 0;
}

.box-banner .icon-abs.icon-3.active {
  opacity: 1;
  top: 0;
  animation: 5s fuwafuwa infinite;
}

.box-banner .icon-abs.icon-4 {
  transition: .9s all !important;
  top: -60px;
  opacity: 0;
}

.box-banner .icon-abs.icon-4.active {
  opacity: 1;
  top: 0;
  animation: 5s fuwafuwa infinite;
}

.bg-orang {
  position: fixed;
  z-index: 12;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #FF4A01;
  transition: 1s all !important;
  pointer-events: none;
}

.bg-orang.active {
  opacity: 0;
}

.bg-cover {
  position: fixed;
  width: 100vh;
  height: 100vh;
  background: #40CE7A;
  top: 0;
  left: calc(50% - 50vh);
  z-index: 13;
  transition: 1.8s all !important;
  transform: scale(0);
  border-radius: 100%;
  pointer-events: none;
}

.bg-cover.active {
  transform: scale(4);
  opacity: 0;
}

@keyframes letter-flicker {
  0% {
    opacity: 0.1;
  }
  23% {
    opacity: 1;
  }
  50% {
    opacity: 0.1;
  }
  75% {
    opacity: 1;
  }
  85% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes title-flicker {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 0.1;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes unyo-right3 {
  0% {
    -webkit-mask-position: 0 0;
    mask-position: 0 0;
  }
  to {
    -webkit-mask-position: 70px 0;
    mask-position: 70px 0;
  }
}

@keyframes scroll-anim {
  100% {
    background-position: 100% 0;
  }
}

@keyframes title_line {
  0% {
    width: 0px;
  }
  100% {
    width: calc(100% + 43px);
    @media (max-width: 767px) {
      width: calc(100% + 20px);
    }
  }
}

.box-banner-mobile {
  display: none;
}

@media (max-width: 767px) {
  .box-banner-mobile {
    display: block;
    position: relative;
    background: #42CD7A;
    width: 100%;
    height: 100vh;
    overflow: hidden;
  }
}

.box-banner-mobile .cover-1 {
  filter: blur(10px);
  transition: 0.8s all !important;
}

.box-banner-mobile .cover-1.active {
  filter: blur(0);
}

.box-banner-mobile .cover-1 img {
  width: 100%;
}

.box-banner-mobile .cover-2 {
  right: 4%;
  max-width: 64%;
  z-index: 2;
  top: 8%;
  margin-top: -40px;
  transition: 1s all !important;
  opacity: 0;
}

.box-banner-mobile .cover-2.active {
  margin-top: 0;
  opacity: 1;
  animation: 5s fuwafuwa infinite;
}

.box-banner-mobile .cover-3 {
  top: 34%;
  right: 6%;
  max-width: 22%;
  z-index: 3;
  margin-top: -40px;
  transition: 1s all !important;
  opacity: 0;
}

.box-banner-mobile .cover-3.active {
  margin-top: 0;
  opacity: 1;
  animation: 5s fuwafuwa infinite;
}

.box-banner-mobile .logo-cover {
  max-width: 146px;
  position: fixed;
  top: -50px;
  left: 17px;
  z-index: 2;
  transition: 1s all !important;
  opacity: 0;
  animation: 5s fuwafuwa infinite;
}

.box-banner-mobile .logo-cover.active {
  opacity: 1;
  top: 7px;
}

.box-banner-mobile .icon-cover {
  display: flex;
  align-items: center;
  justify-content: center;
}

.box-banner-mobile .cover-abs {
  position: absolute;
}

.viewmore a span {
  opacity: 0;
}

.viewmore.animated a span {
  transform: translateY(2em);
  animation: textanimation 1s forwards;
}

/* 1文字目 */
.viewmore.animated a span:nth-child(1) {
  animation-delay: 0.2s;
}

/* 2文字目 */
.viewmore.animated a span:nth-child(2) {
  animation-delay: 0.4s;
}

/* 3文字目 */
.viewmore.animated a span:nth-child(3) {
  animation-delay: 0.6s;
}

/* 4文字目 */
.viewmore.animated a span:nth-child(4) {
  animation-delay: 0.8s;
}

/* 5文字目 */
.viewmore.animated a span:nth-child(5) {
  animation-delay: 1s;
}

/* 6文字目 */
.viewmore.animated a span:nth-child(6) {
  animation-delay: 1.2s;
}

/* 7文字目 */
.viewmore.animated a span:nth-child(7) {
  animation-delay: 1.4s;
}

/* 8文字目 */
.viewmore.animated a span:nth-child(8) {
  animation-delay: 1.6s;
}

/* 9文字目 */
.viewmore.animated a span:nth-child(9) {
  animation-delay: 1.8s;
}

/* 10文字目 */
.viewmore.animated a span:nth-child(10) {
  animation-delay: 2s;
}

/* 11文字目 */
.viewmore.animated a span:nth-child(11) {
  animation-delay: 2.2s;
}

/* 12文字目 */
.viewmore.animated a span:nth-child(12) {
  animation-delay: 2.4s;
}

@keyframes textanimation {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes fuwafuwa {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

@media (max-width: 768px) {
  @keyframes fuwafuwa {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
  }
}
